import React, { useState } from "react";
import Layout from "../components/layout/Layout.jsx";
import Modal from "../components/modal/Modal.jsx";
import TableData from "../components/table/TableData.jsx";
import EditModal from "../components/modal/EditModal.jsx";
import AnalyticsData from "../components/analyticData/analyticsData.jsx";
import { CiViewTable } from "react-icons/ci";
import { IoIosAnalytics } from "react-icons/io";

const HomePage = () => {
  const [openModal, setOpenModal] = useState(false);
  const [frequency, setFrequency] = useState("All");
  const [categoryFilter, setCategoryFilter] = useState("All");
  const [updateModal, setUpdateModal] = useState(false);
  const [transId, setTransId] = useState("");
  const [analyticTrue, setAnalyticTrue] = useState(false);

  return (
    <Layout>
      <div className="w-full h-full">
        <div className="filter-head w-full flex px-4 py-1 justify-between items-end shadow-[0_0_3px_grey] bg-gray-100">
          <div>
            <h6 className="px-2 py-1">Filters</h6>
            <select
              onChange={(e) => setFrequency(e.target.value)}
              className="py-1 px-2 cursor-pointer border"
            >
              <option value="All">All</option>
              <option value="7">Last 1 Week</option>
              <option value="30">Last 1 Month</option>
              <option value="365">Last 1 Year</option>
            </select>
          </div>

          <div>
            <h6 className="px-2 py-1">Type</h6>
            <select
              onChange={(e) => setCategoryFilter(e.target.value)}
              defaultValue={categoryFilter}
              className="py-1 px-2 cursor-pointer border"
            >
              <option value="All">All</option>
              <option value="Income">Income</option>
              <option value="Expense">Expenses</option>
            </select>
          </div>

          <div className="flex border justify-center">
            <button
              className={`p-1 w-8 h-8 ${
                !analyticTrue ? "bg-gray-200" : "bg-transparent"
              }`}
              onClick={() => setAnalyticTrue(false)}
            >
              <CiViewTable className="w-full h-full" />
            </button>
            <button
              className={`p-1 w-8 h-8 ${
                analyticTrue ? "bg-gray-200" : "bg-transparent"
              }`}
              onClick={() => setAnalyticTrue(true)}
            >
              <IoIosAnalytics className="w-full h-full" />
            </button>
          </div>

          <button
            className="px-4 py-1 bg-blue-800 text-white rounded-md"
            onClick={() => setOpenModal(true)}
          >
            Add New
          </button>
        </div>
        <div className="w-full h-full">
          {analyticTrue ? (
            <AnalyticsData
              frequency={frequency}
              categoryFilter={categoryFilter}
              openModalFn={openModal}
            />
          ) : (
            <TableData
              frequency={frequency}
              setTransId={setTransId}
              updateModalChange={setUpdateModal}
              categoryFilter={categoryFilter}
              openModalFn={openModal}
              updateModalFn={updateModal}
            />
          )}
        </div>
      </div>
      {openModal && <Modal changeModal={setOpenModal} />}

      {updateModal && (
        <EditModal transId={transId} updateModalChange={setUpdateModal} />
      )}
    </Layout>
  );
};

export default HomePage;
