import { useEffect, useState } from "react";
import { Link, useNavigate } from "react-router-dom";
const Header = () => {
  const navigate = useNavigate();
  const [loginUser, setUserLogin] = useState("");

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    setUserLogin(user);
  }, [navigate]);

  const logoutHandler = () => {
    localStorage.removeItem("user");
    navigate("/login");
  };

  return (
    <>
      <nav className="w-full h-[60px] bg-[black] flex justify-between items-center px-8">
        <div className="flex h-full justify-start items-center px-4">
          <Link to="/">
            <h3 className="tracking-[5px] text-[orange] text-[36px]">
              expense
            </h3>
          </Link>
        </div>
        <ul className="gap-10 h-full flex justify-end items-center">
          <li className="font-bold text-white">
            <Link to="/user">{loginUser && loginUser.user}</Link>
          </li>
          <li>
            <button
              onClick={logoutHandler}
              className="font-bold text-white px-4 py-2 bg-blue-800 rounded"
            >
              <Link to="/">{loginUser ? "Logout" : "User"}</Link>
            </button>
          </li>
        </ul>
      </nav>
    </>
  );
};

export default Header;
