import React, { useEffect, useState } from "react";
import Layout from "../components/layout/Layout";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";
import loginImg from "../assets/login-img.png";

const Login = () => {
  const [loginEmail, setLoginEmail] = useState("");
  const [loginPassword, setLoginPassword] = useState("");
  const navigate = useNavigate();

  async function loginHandler(e) {
    e.preventDefault();
    const loginDetails = {
      loginEmail,
      loginPassword,
    };
    try {
      const response = await axios.post("api/v1/users/login", loginDetails);
      localStorage.setItem(
        "user",
        JSON.stringify({
          user: response.data.user.name,
          userId: response.data.user._id,
          email: response.data.user.email,
        })
      );
      toast.success("logged in successfully", { position: "top-center" });
      navigate("/");
    } catch (error) {
      toast.error("Email address or password is incorrect!", {
        position: "top-center",
      });
    }
  }

  useEffect(() => {
    if (localStorage.getItem("user")) {
      navigate("/");
    }
  }, [navigate]);

  return (
    <Layout>
      <div className="w-full h-[80vh] flex gap-16 justify-center items-center">
        <div className="login-img p-4">
          <img src={loginImg} alt="loginImg" className="w-[400px] h-[400px]" />
        </div>

        <div>
          <form
            onSubmit={loginHandler}
            className="w-[400px] h-[400px] flex items-center justify-center border flex-col gap-4 rounded shadow-md p-6"
          >
            <h2 className="w-full text-center text-2xl text-black font-bold">
              Login
            </h2>
            <div className="flex flex-col w-full">
              <label>Email</label>
              <input
                required
                type="email"
                onChange={(e) => setLoginEmail(e.target.value)}
                placeholder="Enter email address"
                className="border rounded p-2"
              />
            </div>
            <div className="flex flex-col  w-full">
              <label>Passwrod</label>
              <input
                required
                type="password"
                onChange={(e) => setLoginPassword(e.target.value)}
                placeholder="Enter password"
                className="border rounded p-2"
              />
            </div>
            <div className="flex justify-center  w-full">
              <p>
                Don't have an account?{" "}
                <Link className="text-blue-500" to="/register">
                  Register
                </Link>
              </p>
            </div>
            <button className="px-4 hover:shadow-lg bg-blue-600 text-white rounded py-2">
              Login
            </button>
          </form>
        </div>
      </div>
    </Layout>
  );
};

export default Login;
