import React, { useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";

const Modal = ({ changeModal }) => {
  const [amount, setAmount] = useState("");
  const [category, setCategory] = useState("Personal");
  const [date, setDate] = useState("");
  const [type, setType] = useState("Expense");
  const [description, setDescription] = useState("");

  // Add user Handler
  const newTransactionHandler = async (e) => {
    e.preventDefault();
    const userId = JSON.parse(localStorage.getItem("user")).userId;
    const transactionDetails = {
      userId,
      amount,
      category,
      date,
      type,
      description,
    };

    try {
      await axios.post(
        "api/v1/transactions/addtransaction",
        transactionDetails
      );
      toast.success("Transaction added successfully", {
        position: "top-center",
      });
      changeModal(false);
    } catch (error) {
      toast.error("Failed to add transaction", { position: "top-center" });
    }
  };

  return (
    <div className="modal-main">
      <div className="modal">
        <div className="flex justify-between">
          <h4 className="font-bold py-4">Add New Transaction</h4>
          <button
            onClick={() => {
              changeModal(false);
            }}
            className="font-bold text-xl"
          >
            x
          </button>
        </div>
        <hr />
        <form onSubmit={newTransactionHandler} className="py-4">
          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Amount</label>
            <input
              required
              onChange={(e) => setAmount(e.target.value)}
              type="number"
              className="border rounded px-2 py-2"
            />
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Type</label>
            <select
              required
              onChange={(e) => setType(e.target.value)}
              defaultValue={type}
              className="border cursor-pointer rounded px-2 py-2"
            >
              <option value="Expense">Expense</option>
              <option value="Income">Income</option>
            </select>
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Category</label>
            <select
              required
              onChange={(e) => setCategory(e.target.value)}
              className="border rounded cursor-pointer px-2 py-2"
              defaultValue={category}
            >
              <option value="Personal">Personal</option>
              <option value="Shopping">Shopping</option>
              <option value="Food">Food</option>
              <option value="Bills">Bills</option>
              <option value="Salary">Salary</option>
              <option value="Medical">Medical</option>
              <option value="Home">Home</option>
              <option value="Others">Others</option>
            </select>
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Date</label>
            <input
              required
              onChange={(e) => setDate(e.target.value)}
              type="date"
              className="border rounded cursor-pointer px-2 py-2"
            />
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Description</label>
            <input
              required
              onChange={(e) => setDescription(e.target.value)}
              type="text"
              className="border rounded px-2 py-2"
            />
          </div>

          <div className="flex justify-center">
            <button className="px-8 py-2 bg-blue-800 text-white rounded-md">
              Add
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Modal;
