import React, { useEffect, useState } from "react";
import Layout from "../components/layout/Layout";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";
import { toast } from "react-toastify";

const Register = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const navigate = useNavigate();
  const registerHandler = async (e) => {
    e.preventDefault();
    const registerDetails = {
      name,
      email,
      password,
    };
    try {
      await axios.post("api/v1/users/register", registerDetails);
      toast.success("Registered successfully", { position: "top-center" });
      navigate("/login");
    } catch (error) {
      toast.error(error, { position: "top-center" });
    }
  };

  useEffect(() => {
    if (localStorage.getItem("user")) {
      navigate("/");
    }
  }, [navigate]);

  return (
    <Layout>
      <form
        onSubmit={registerHandler}
        className="mt-4 w-[500px] h-[450px] flex items-center justify-center flex-col gap-6 rounded border shadow-md p-8"
      >
        <h1 className="w-full text-center text-3xl text-black font-bold">
          Sign Up
        </h1>
        <div className="flex flex-col w-full">
          <label>Name</label>
          <input
            required
            type="text"
            onChange={(e) => setName(e.target.value)}
            placeholder="Enter name"
            className="border rounded p-2"
          />
        </div>
        <div className="flex flex-col w-full">
          <label>Email</label>
          <input
            required
            type="email"
            onChange={(e) => setEmail(e.target.value)}
            placeholder="Enter email address"
            className="border rounded p-2"
          />
        </div>
        <div className="flex flex-col w-full">
          <label>Passwrod</label>
          <input
            required
            type="password"
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Enter password"
            className="border rounded p-2"
          />
        </div>
        <div className="flex justify-center w-full">
          <p>
            Already have an account?{" "}
            <Link className="text-blue-500" to="/login">
              Login
            </Link>
          </p>
        </div>
        <button className="px-4 hover:shadow-lg bg-orange-600 text-white rounded py-2">
          Register
        </button>
      </form>
    </Layout>
  );
};

export default Register;
