import userImg from "../assets/user.png";
import Layout from "../components/layout/Layout";
import { toast } from "react-toastify";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const UserPage = () => {
  const navigate = useNavigate();
  const user = JSON.parse(localStorage.getItem("user"));

  const deleteUserHandler = async () => {
    try {
      await axios.delete(`api/v1/users/user/${user.userId}`);
      toast.success("Account deleted successfully");
      navigate("/login");
      localStorage.removeItem("user");
    } catch (error) {
      toast.error("Failed to delete the account");
    }
  };

  return (
    <Layout>
      <div className="w-full h-[100vh] bg-gray-100 flex flex-col py-10 items-center">
        <div className="p-8 w-full flex justify-center">
          <img src={userImg} alt="User-image" className="w-40 h-40" />
        </div>
        <h4 className="w-full text-center font-bold text-lg py-4">
          User Name: {user.user}
        </h4>
        <h1 className="w-full text-center font-bold text-lg py-4">
          User Email Address : {user.email}
        </h1>
        <button
          className="px-6 py-4 bg-red-600 text-white hover:shadow-lg rounded-md"
          onClick={deleteUserHandler}
        >
          Delete Account
        </button>
      </div>
    </Layout>
  );
};

export default UserPage;
