import React, { useEffect, useState } from "react";
import axios from "axios";
import { toast } from "react-toastify";
import moment from "moment";

const EditModal = ({ transId, updateModalChange }) => {
  const [amount, setAmount] = useState("");
  const [category, setCategory] = useState("");
  const [date, setDate] = useState("");
  const [type, setType] = useState("");
  const [description, setDescription] = useState("");

  useEffect(() => {
    getTransactionById();
  }, [transId]);

  async function getTransactionById() {
    const res = await axios.get(`api/v1/transactions/transaction/${transId}`);
    const getDetails = res.data.data;
    setAmount(getDetails.amount);
    setCategory(getDetails.category);
    setDate(moment(getDetails.date).format("YYYY-MM-DD"));
    setType(getDetails.type);
    setDescription(getDetails.description);
  }

  // Modal Handler
  const editTransactionHandler = async (e) => {
    e.preventDefault();
    const updateDetails = {
      amount,
      category,
      date,
      type,
      description,
    };
    try {
      await axios.patch(
        `api/v1/transactions/transaction/${transId}`,
        updateDetails
      );
      toast.success("Updated successfully", {
        position: "top-center",
      });
      updateModalChange(false);
    } catch (error) {
      console.log(error);
      toast.error("Update failed", { position: "top-center" });
    }
  };

  return (
    <div className="modal-main">
      <div className="modal">
        <div className="flex justify-between">
          <h4 className="font-bold py-4">Update Transaction</h4>
          <button
            onClick={() => {
              updateModalChange(false);
            }}
            className="font-bold text-xl"
          >
            x
          </button>
        </div>
        <hr />
        <form onSubmit={editTransactionHandler} className="py-4">
          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Amount</label>
            <input
              required
              onChange={(e) => setAmount(e.target.value)}
              type="number"
              defaultValue={amount}
              className="border rounded px-2 py-2"
            />
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Type</label>
            <select
              required
              onChange={(e) => setType(e.target.value)}
              value={type}
              className="border cursor-pointer rounded px-2 py-2"
            >
              <option value="Expense">Expense</option>
              <option value="Income">Income</option>
            </select>
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Category</label>
            <select
              required
              onChange={(e) => setCategory(e.target.value)}
              className="border cursor-pointer rounded px-2 py-2"
              value={category}
            >
              <option value="Personal">Personal</option>
              <option value="Shopping">Shopping</option>
              <option value="Food">Food</option>
              <option value="Bills">Bills</option>
              <option value="Salary">Salary</option>
              <option value="Medical">Medical</option>
              <option value="Home">Home</option>
              <option value="Others">Others</option>
            </select>
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Date</label>
            <input
              required
              onChange={(e) => setDate(e.target.value)}
              type="date"
              defaultValue={date}
              className="border cursor-pointer rounded px-2 py-2"
            />
          </div>

          <div className="flex flex-col gap-1 mb-4">
            <label htmlFor="">Description</label>
            <input
              required
              onChange={(e) => setDescription(e.target.value)}
              type="text"
              defaultValue={description}
              className="border rounded px-2 py-2"
            />
          </div>

          <div className="flex justify-center">
            <button className="px-8 py-2 bg-blue-800 text-white rounded-md">
              Save
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default EditModal;
