import React, { useEffect, useState } from "react";
import axios from "axios";
import { Progress } from "antd";

const AnalyticsData = ({ frequency, categoryFilter, openModalFn }) => {
  const [allTransactions, setAllTransactions] = useState([]);

  useEffect(() => {
    showTransactions();
  }, [frequency, categoryFilter, openModalFn]);

  async function showTransactions() {
    const user = JSON.parse(localStorage.getItem("user"));
    const res = await axios.post("api/v1/transactions/alltransactions", {
      userId: user.userId,
      frequency,
      categoryFilter,
    });
    setAllTransactions(res.data.transactions);
  }

  const totalTransactions = allTransactions.length;
  const totalIncomeTransactions = allTransactions.filter(
    (tr) => tr.type === "Income"
  );
  const totalExpenseTransactions = allTransactions.filter(
    (tr) => tr.type === "Expense"
  );

  const expensePercent =
    (totalIncomeTransactions.length / totalTransactions) * 100;
  const incomePercent =
    (totalExpenseTransactions.length / totalTransactions) * 100;

  // Turn Over Calculations
  const totalTurnover = allTransactions.reduce(
    (accu, transaction) => accu + transaction.amount,
    0
  );
  const TotalIncomeValue = totalIncomeTransactions.reduce(
    (accu, transaction) => accu + transaction.amount,
    0
  );
  const TotalExpenseValue = totalExpenseTransactions.reduce(
    (accu, transaction) => accu + transaction.amount,
    0
  );

  // // CategoryWise
  // const categories = [
  //   "Personal",
  //   "Shopping",
  //   "Food",
  //   "Bills",
  //   "Salary",
  //   "Medical",
  //   "Home",
  //   "Others",
  // ];

  return (
    <div className="flex flex-col w-full h-full">
      <h2 className="text-center text-2xl font-bold py-4">Analytics</h2>
      <div className="flex justify-between w-full h-[100%] px-6">
        {/* Total Transactions */}
        <div className="w-[48%] h-[auto] border rounded pb-4">
          <h4 className="border bg-slate-100 font-bold py-1 pl-2  ">
            Total Transactions : {totalTransactions}{" "}
          </h4>
          <h4 className="pl-2 text-sm">
            Total Income Transactions : {totalIncomeTransactions.length}{" "}
          </h4>
          <h4 className="pl-2 text-sm">
            Total Expense Transactions : {totalExpenseTransactions.length}{" "}
          </h4>
          <div className="transaction-div flex justify-evenly">
            <div className="p-4">
              <h4 className="py-2 text-center">Expense Transactions %</h4>
              <Progress
                type="circle"
                percent={expensePercent.toFixed(0)}
                className=""
              />
            </div>
            <div className="p-4">
              <h4 className="py-2 text-center">Income Transactions %</h4>
              <Progress
                type="circle"
                percent={incomePercent.toFixed(0)}
                className="w-[150px] h-[150px]"
              />
            </div>
          </div>
        </div>

        {/* Total  Turnover */}
        <div className="w-[48%] h-[auto] border rounded pb-4">
          <h4 className="border bg-slate-100 font-bold py-1 pl-2  ">
            Total Turnover : {totalTurnover}
          </h4>
          <h4 className="pl-2 text-sm">
            Total Income Value : {TotalIncomeValue}
          </h4>
          <h4 className="pl-2 text-sm">
            Total Expense Value : {TotalExpenseValue}
          </h4>
          <div className="value-div flex justify-evenly">
            <div className="p-4">
              <h4 className="py-2 text-center">Expense %</h4>
              <Progress
                type="circle"
                percent={Math.round((TotalExpenseValue / totalTurnover) * 100)}
                className="w-[150px] h-[150px]"
              />
            </div>
            <div className="p-4">
              <h4 className="py-2 text-center">Income %</h4>
              <Progress
                type="circle"
                percent={Math.round((TotalIncomeValue / totalTurnover) * 100)}
                className="w-[150px] h-[150px]"
              />
            </div>
          </div>
        </div>

        {/*  Calculating values of each category */}

        {/* <div  className="w-[400px] h-[350px] border rounded pb-4">
{  categories.map((category) => {
  const categoryAmount = allTransactions.filter((transaction) => transaction.type === 'Income' && transaction.category === category).reduce((accu, trans) => accu + trans.amount, 0)

  return (
  <div>
    <h5>{category}</h5>
    <Progress percent={((categoryAmount / totalTurnover) * 100).toFixed(0)} />
  </div>
  )
  })}
        </div> */}
      </div>
    </div>
  );
};

export default AnalyticsData;
