import React, { useEffect, useState } from "react";
import axios from "axios";
import moment from "moment";
import { toast } from "react-toastify";
import editPng from "../../assets/edit.png";
import deletePng from "../../assets/delete.png";

const TableData = ({
  frequency,
  categoryFilter,
  updateModalChange,
  setTransId,
  openModalFn,
  updateModalFn,
}) => {
  const [currentPage, setCurrentPage] = useState(1);
  const dataPerPage = 10;

  const [allTransactions, setAllTransactions] = useState([]);
  useEffect(() => {
    showTransactions();
  }, [frequency, categoryFilter, openModalFn, updateModalFn]);

  async function showTransactions() {
    const user = JSON.parse(localStorage.getItem("user"));
    const res = await axios.post("api/v1/transactions/alltransactions", {
      userId: user.userId,
      frequency,
      categoryFilter,
    });
    setAllTransactions(res.data.transactions);
  }
  const totalPages = Math.ceil(allTransactions.length / dataPerPage);

  // Nex and Prev Page

  // Edit Transaction
  function EditTransactionHandler(id) {
    setTransId(id);
    updateModalChange(true);
  }

  // Delete transaction
  async function deleteTransactionHandler(id) {
    try {
      await axios.delete(`api/v1/transactions/transaction/${id}`);
      showTransactions();
      toast.success("Deleted Successfully", { position: "top-center" });
    } catch (error) {
      toast.error("Failed to Delete", { position: "top-center" });
    }
  }

  return (
    <>
      <div className="w-full h-full">
        <table className="data-table">
          <thead>
            <tr>
              <th>Sl No.</th>
              <th>Date</th>
              <th>Amount</th>
              <th>Type</th>
              <th>Category</th>
              <th>Description</th>
              <th>Action</th>
            </tr>
          </thead>
          <tbody>
            {allTransactions
              .slice(
                currentPage * dataPerPage - dataPerPage,
                currentPage * dataPerPage
              )
              .map((transaction, index) => (
                <tr key={transaction._id}>
                  <td>
                    {currentPage * dataPerPage - dataPerPage + (index + 1)}
                  </td>
                  <td>{moment(transaction.date).format("DD-MM-YYYY")}</td>
                  <td>{transaction.amount}</td>
                  <td>{transaction.type}</td>
                  <td>{transaction.category}</td>
                  <td>{transaction.description}</td>
                  <td>
                    <div>
                      <button
                        onClick={() => EditTransactionHandler(transaction._id)}
                      >
                        <img src={editPng} alt="editPng" className="w-5 h-5" />
                      </button>
                      <button
                        onClick={() =>
                          deleteTransactionHandler(transaction._id)
                        }
                      >
                        <img
                          src={deletePng}
                          alt="editPng"
                          className="w-5 h-5"
                        />
                      </button>
                    </div>
                  </td>
                </tr>
              ))}
          </tbody>
        </table>
        <div className="h-auto py-4 flex justify-center gap-2">
          {totalPages > 1 && currentPage > 1 && (
            <button
              className="px-4 py-1 bg-blue-600 text-white"
              onClick={() => setCurrentPage(currentPage - 1)}
            >
              Prev
            </button>
          )}

          {[...Array(totalPages)].map((_, i) => {
            return (
              <button
                key={i}
                className={`px-4 py-1 text-black ${
                  currentPage == i + 1
                    ? "bg-blue-600 text-white"
                    : "bg-gray-300"
                }`}
                onClick={() => setCurrentPage(i + 1)}
              >
                {i + 1}
              </button>
            );
          })}

          {totalPages > 1 && currentPage < totalPages && (
            <button
              className="px-4 py-1 bg-blue-600 text-white active:bg-blue-700"
              onClick={() => setCurrentPage(currentPage + 1)}
            >
              Next
            </button>
          )}
        </div>
      </div>
    </>
  );
};

export default TableData;
